/* scss */

$accentcolour: blue;
$bodycolor: white;
$normalfont: 'Roboto', arial, helvetica, sans-serif;
$fontserif: serif;
$smallspace: 3vw;


/* http://meyerweb.com/eric/tools/css/reset/ 
 v2.0 | 20110126
 License: none (public domain)
*/

a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,canvas,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video{margin:0;padding:0;border:0;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

body,html {
 margin: 0;
 padding: 0;
 min-height: 100%;
 font-family: $normalfont;
 font-size: 16px;
 background: #222;
}

body {
 font-size: 1.2em;
 color: $bodycolor;
 line-height: 1.6;
 -webkit-font-smoothing: antialiased;
}
* {
 margin: 0;
 padding: 0;
}

*,
:after,
:before {
 -webkit-box-sizing: border-box;
 -moz-box-sizing: border-box;
 box-sizing: border-box;
}

img,
embed,
object,
video {
 max-width: 100%;
 border: 0;
}
hr {
 display: block;
 height: 3px;
 margin: 3em 0;
 padding: 0;
 border: 0;
 border-top: 3px solid $bodycolor;
}

p {
 margin-bottom: 1em;
 line-height: 1.6;

}

.small {
 line-height: 1.3;
 margin-bottom: 20px;
 font-size: .8em;
}
.small a {
 text-decoration: none;
}
.large {
 margin: 0 auto;
 margin-top: 30px;
 margin-bottom: 50px;
 font-size: 1.6em;
 padding: 15px;
}
.large p {
 line-height: 1.7;
}
.large a {
 border-bottom: .25vw solid white;
 text-decoration:none !important;
 padding-bottom: .25vw;

}





b, strong {
 font-weight: 700;
}

a {
 text-decoration: none;
 color: $bodycolor;
}

p a {
 color: $bodycolor;
 text-decoration: underline;
}



i, em {
 font-style: italic;
}

sup { 
 vertical-align: super;
 font-size: .6em;
}



h1, h2, h3, h4, h5, h6 {
 font-weight: 700;
 color: $bodycolor;
}

h1 {
 font-size: 3.5em;
 margin-bottom: 30px;
 text-align: center;
}

h2 {
 font-size: 3em;
 margin-bottom: 20px;
}

h3 {
 font-size: 2em;
 margin-bottom: 20px;
}

h4 {
 font-size: 1.6em;
}

h5 {
 text-align: center;
 font-size: 1em;
 text-transform: uppercase;
}

h6 {
 font-size: 2em;
}


.tc {text-align: center;}
.tl {text-align: left;}
.tr {text-align: right;}


ul {

}

li {

}

/* Main Elements Styles
---------------------------------------------- */


.container {
 margin: 0;
 padding: 0;
 width: 100%;
}

.content {
 max-width: 800px;
 margin: 30vh auto;
 padding: 0em 2em;
}


/* SpanX based grid elements */

.row:before,
.row:after {
 display: table;
 line-height: 0;
 content: "";
}

.row {
 margin: 0 -1em;
}

.row:after {
 clear: both;
}

[class*="span"] {
 display: block;
 float: left;
 padding: 0 1em;
 width: 100%;
 margin: 0 0 1em 0;
}

[class*="span"] img {
 margin: 0;
 height: auto;
 padding: 0;
 width: 100%;
}

.span1{width:8.3333%}
.span2{width:16.666667%}
.span3{width:25%}
.span4{width:33.333%}
.span5{width:41.6667%}
.span6{width:50%}
.span7{width:58.3333%}
.span8{width:66.6667%}
.span9{width:75%}
.span10{width:83.3333%}
.span11{width:91.6667%}
.span12{width:100%}
.offset1{margin-left:8.3333%}
.offset2{margin-left:16.666667%}
.offset3{margin-left:25%}
.offset4{margin-left:33.333%}
.offset5{margin-left:41.6667%}
.offset6{margin-left:50%}
.offset7{margin-left:58.3333%}
.offset8{margin-left:66.6667%}
.offset9{margin-left:75%}
.offset10{margin-left:83.3333%}
.offset11{margin-left:91.6667%}
.offset12{margin-left:100%}

/* END -- SpanX based grid elements */



/* Primary site sections
---------------------------------------------- */




/** MEDIA QUERIES **/

@media only screen and (max-width: 1200px) {
 /* tablets and desktop */
 body {
 font-size: 1.2em;
 }

 .span3 {
 width: 33.33%;
 }
 .span4 {
 width: 50%;
 }
}
@media only screen and (max-width: 1024px) {
 /* tablets and desktop */
 body {
 font-size: 1.2em;
 }

 .span3 {
 width: 33.33%;
 }
 .span4 {
 width: 50%;
 }
}

@media only screen and (max-width: 767px) {
 /* phones */
 body {
 font-size: .9em;
 }

 .span4 {
 width: 100%;
 }
 .span3 {
 width: 50%;
 }

 [class*='offset'] {
 margin-left: 0;
 }

}





@media only screen and (max-width: 480px) {
 body {
 font-size: .8em;
 }

 .two-col {
 column-count: 1;
 column-gap: 0px;
 }
 [class*='span'] {
 width: 100%;
 }


}













